import { DataUserModel } from 'api/auth/models/DataUserModel';
import { ApiResponse } from 'api/RootApi/ApiStore/types';
import React from 'react';
import { useSelector } from 'react-redux';
import { getFavoritesIds } from 'store/favorites/FavoritesStore';
import { getCheckoutProduct } from 'store/product/CheckoutProductStore';
import { changeToken, getUserData } from 'store/user/UserDataStore';
import useAppDispatch from './useAppDispatch';

export const useGetUserData = () => {
	const dispatch = useAppDispatch();

	const tokenStore = useSelector((state: any) => state.userData.token);

	React.useEffect(() => {
		const token = localStorage.getItem('token');
		if (token && !tokenStore) {
			dispatch(getUserData({ token })).then(res => {
				const { data } = res.payload as ApiResponse<DataUserModel, DataUserModel>;
				if (data.message) {
					localStorage.removeItem('token');
				} else {
					!tokenStore && dispatch(changeToken(token));
					dispatch(getCheckoutProduct({ token }));
					dispatch(getFavoritesIds({ token }));
				}
			});
		} else {
			dispatch(getCheckoutProduct({ token: null }));
			dispatch(getFavoritesIds({ token }));
		}
	}, []);
};
