import React from 'react';

import styles from './Col.module.scss';

type ColTypes = {
	marginBottom?: number;
	marginTop?: number;
	className?: string;
	colRef?: React.RefObject<HTMLDivElement>;
	children: React.ReactElement<any, any>;
};

const Col = ({ marginBottom, marginTop, className, colRef, children }: ColTypes) => {
	return (
		<div
			ref={colRef}
			style={{
				marginBottom: typeof marginBottom === 'number' ? `${marginBottom}px` : null,
				marginTop: marginTop ? `${marginTop}px` : null,
			}}
			className={`${styles.col} ${className ? className : ''}`}
		>
			{children}
		</div>
	);
};

export default React.memo(Col);
