import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, RestoreApiData, IRestoreApi, RestoreResponseData } from '../RestoreApi/types';

export default class RestoreApi implements IRestoreApi {
	private readonly _apiStore = RootStore.apiStore;

	async getRestore(data: RestoreApiData): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<RestoreResponseData, RestoreResponseData>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/auth/restore`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
