import Footer from 'components/Footer';
import Header from 'components/Header';
import ChangePassword from 'components/pages/ChangePassword';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { useGetUserData } from 'helpers/hooks/useGetUserData';
import { baseSSR } from 'helpers/utils/baseSSR';
import { serverSideTranslations } from 'next-i18next/serverSideTranslations';
import Head from 'next/head';
import React from 'react';
import { changeIsMobile } from 'store/isMobile/IsMobileStore';

const ChangePasswordPage = ({ data }) => {
	const dispatch = useAppDispatch();

	useGetUserData();

	dispatch(changeIsMobile(data.isMobile));

	return (
		<>
			<Head>
				<meta
					name="description"
					content="TOTTI I Базовая одежда, отражающая характер. Одежда и аксессуары для женщин, детей и мужчин. Актуальность, комфорт и качество."
				></meta>
			</Head>
			<Header />
			<ChangePassword />
			<Footer withDarkBGC={true} />
		</>
	);
};

export const getServerSideProps = async ({ req, locale }) => {
	const { isMobile } = await baseSSR(req);
	return {
		props: {
			data: {
				isMobile,
			},
			...(await serverSideTranslations(locale, ['common'])),
		},
	};
};

export default ChangePasswordPage;
