import { SuisseIntl } from 'helpers/utils/fonts';
import React from 'react';
import DOMPurify from 'isomorphic-dompurify';

import style from './Title.module.scss';

type TitleProps = {
	id?: string;
	size?: number;
	text?: any;
	withDot?: boolean;
	withSuisse?: boolean;
	className?: string;
	count?: number;
	marginBottom?: number;
	children?: React.ReactChildren | string;
};

const Title: React.FC<TitleProps> = ({
	id,
	size,
	text,
	withDot,
	withSuisse,
	className,
	count,
	marginBottom,
	children,
}: TitleProps) => {
	return (
		<h1
			style={
				size
					? { fontSize: `${size}px`, marginBottom: Number.isInteger(marginBottom) ? `${marginBottom}px` : null }
					: { marginBottom: Number.isInteger(marginBottom) ? `${marginBottom}px` : null }
			}
			id={id}
			className={`${style.title} ${className ? className : ''} ${withSuisse ? SuisseIntl.className : ''}`}
		>
			{withDot ? '· ' : ''}
			{children ? children : text}
			<sup
				className={`${style.sub} ${size <= 40 ? style.miniSub : size > 70 ? style.bigSub : ''}`}
				dangerouslySetInnerHTML={
					count !== undefined && count !== null
						? {
								__html: DOMPurify.sanitize(
									`&nbsp;(${count.toString().length === 1 ? '0' + count.toString() : count})`
								),
						  }
						: { __html: '' }
				}
			/>
		</h1>
	);
};

export default React.memo(Title);
