import React from 'react';
import showInput from 'public/static/img/showInput.svg';
import hiddenInput from 'public/static/img/hiddenInput.svg';
import pencil from 'public/static/img/pencil.svg';

import styles from './Input.module.scss';
import Image from 'next/image';

type InputTypes = {
	refInput?: React.RefObject<HTMLDivElement>;
	value?: string;
	type?: string;
	id?: string;
	placeholder: string;
	label?: string;
	isError?: boolean;
	errorText?: string;
	className?: string;
	readonly?: boolean;
	onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
};

const Input = ({
	refInput,
	value,
	placeholder,
	label,
	type,
	id,
	isError,
	errorText,
	className,
	readonly,
	onChange,
}: InputTypes) => {
	const [isShowPass, setIsShowPass] = React.useState<boolean>(false);
	const [isReadonly, setIsReadonly] = React.useState<boolean>(readonly);

	const inputRef = React.useRef<HTMLInputElement | null>(null);

	const onShowInputHandler = () => {
		setIsShowPass(!isShowPass);
	};

	const editInputHandler = () => {
		setIsReadonly(!isReadonly);
		if (readonly && inputRef.current) {
			if (inputRef.current.readOnly) {
				inputRef.current.readOnly = false;
				inputRef.current.focus();
			} else {
				inputRef.current.readOnly = true;
				inputRef.current.blur();
			}
		}
	};

	React.useEffect(() => {
		setIsReadonly(readonly);
	}, [readonly]);

	return (
		<>
			<div ref={refInput} className={`${styles.inputBlock} ${isError ? styles.isError : ''} ${className ? className : ''}`}>
				<input
					ref={inputRef}
					autoComplete="off"
					className={`${styles.input} ${!readonly ? styles.inputWithoutPencil : ''} ${
						!isReadonly ? styles.inputWithPencil : ''
					}`}
					value={value}
					id={id}
					placeholder={placeholder}
					readOnly={readonly}
					type={type === 'password' && isShowPass ? 'text' : type}
					onChange={onChange}
				/>
				{type === 'password' && value && (
					<Image
						alt="show"
						src={isShowPass ? hiddenInput : showInput}
						width={20}
						height={20}
						onClick={onShowInputHandler}
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
				)}
				{readonly && value && (
					<Image
						alt="pencil"
						src={pencil}
						width={20}
						height={20}
						onClick={editInputHandler}
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
				)}
			</div>
			{label && <p className={styles.label}>{label}</p>}
			{isError && errorText && <p className={styles.errorText}>{errorText}</p>}
		</>
	);
};

export default React.memo(Input);
