import RestoreApi from 'api/auth/RestoreApi';
import Button from 'components/UI/Button';
import Col from 'components/UI/Col';
import Content from 'components/UI/Content';
import Input from 'components/UI/Input';
import Title from 'components/UI/Title';
import routes from 'helpers/routes';
import { useRouter } from 'next/router';
import React from 'react';
import { useSelector } from 'react-redux';

import styles from './ChangePassword.module.scss';
import useAppDispatch from 'helpers/hooks/useAppDispatch/useAppDispatch';
import { getTokenWithEmail, getUserData } from 'store/user/UserDataStore';
import { ApiResponse } from 'api/RootApi/ApiStore/types';
import { SignInModel } from 'api/auth/models/SignInModel';
import { getCheckoutProduct } from 'store/product/CheckoutProductStore';
import { getFavoritesIds } from 'store/favorites/FavoritesStore';

const restoreApi = new RestoreApi();

const ChangePassword = () => {
	const [newPassword, setNewPassword] = React.useState<string>('');
	const [newPasswordConfirmation, setNewPasswordConfirmation] = React.useState<string>('');
	const [isNewPasswordError, setIsNewPasswordError] = React.useState<boolean>(false);
	const [isNewPasswordConfirmationError, setIsNewPasswordConfirmationError] = React.useState<boolean>(false);
	const [errorText, setErrorText] = React.useState<string>('');
	const [userCheckWord, setUserCheckWord] = React.useState<string>('');
	const [userLogin, setUserLogin] = React.useState<string>('');
	const [isLoaded, setIsLoaded] = React.useState<boolean>(true);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const router = useRouter();

	const dispatch = useAppDispatch();

	const changeNewPassword = (event: React.ChangeEvent<HTMLInputElement>) => {
		setNewPassword(event.target.value);
		setIsNewPasswordError(false);
	};

	const changeNewPasswordConfirmation = (event: React.ChangeEvent<HTMLInputElement>) => {
		setNewPasswordConfirmation(event.target.value);
		setIsNewPasswordConfirmationError(false);
	};

	const sendNewPasswordHandler = () => {
		if (newPassword !== newPasswordConfirmation) {
			setIsNewPasswordError(true);
			setIsNewPasswordConfirmationError(true);
			setErrorText('Пароли не совпадают');
			return;
		}
		if (newPassword.length < 8) {
			setIsNewPasswordError(true);
			setErrorText('Пароль должен быть более 8 символов');
			return;
		}
		setIsLoaded(false);
		restoreApi
			.getRestore({
				email: userLogin,
				checkWord: userCheckWord,
				password: newPassword,
				passwordConfirmation: newPasswordConfirmation,
			})
			.then(res => {
				if (res.data.errors) {
					setIsLoaded(true);
					if (res.data.errors.password) {
						setIsNewPasswordError(true);
						setErrorText(res.data.errors.password[0]);
					}
					if (res.data.errors.passwordConfirmation) {
						setIsNewPasswordConfirmationError(true);
						setErrorText(res.data.errors.passwordConfirmation[0]);
					}
				} else if (res.data.message === 'ok') {
					dispatch(getTokenWithEmail({ email: userLogin, password: newPassword, rememberMe: true })).then(res => {
						const { data } = res.payload as ApiResponse<SignInModel, SignInModel>;
						setIsLoaded(true);
						if (data.token) {
							localStorage.setItem('token', data.token);
							dispatch(getUserData({ token: data.token })).then(() => {
								router.push(routes.home + routes.lk);
								dispatch(getCheckoutProduct({ token: localStorage.getItem('token') }));
								dispatch(getFavoritesIds({ token: localStorage.getItem('token') }));
							});
						}
					});
				}
			});
	};

	React.useEffect(() => {
		if (router.query.USER_CHECKWORD) setUserCheckWord(router.query.USER_CHECKWORD as string);
		if (router.query.USER_LOGIN) setUserLogin(router.query.USER_LOGIN as string);
	}, [router.query]);

	return (
		<Content className={styles.content}>
			<Col>
				<>
					<Title text="Восстановление пароля" size={isMobile ? 22 : 28} marginBottom={isMobile ? 32 : 40} withSuisse />
					<Input
						value={newPassword}
						placeholder="Новый пароль"
						type="password"
						errorText={errorText}
						isError={isNewPasswordError}
						onChange={changeNewPassword}
					/>
					<Input
						value={newPasswordConfirmation}
						placeholder="Повторите новый пароль"
						type="password"
						errorText={errorText}
						isError={isNewPasswordConfirmationError}
						onChange={changeNewPasswordConfirmation}
					/>
					<Button onClick={sendNewPasswordHandler} isActive isInvisible text="Сохранить пароль" disabled={!isLoaded} />
				</>
			</Col>
		</Content>
	);
};

export default ChangePassword;
